@include desktop {
    .contenido_principal{
        display: grid;
        grid-template-columns: 2fr 1fr ;
        column-gap: 6em;
    }

    .boton--blog{
        display: inline-block;
    }
}

.entrada{
    border-bottom: 1px solid gray ;
    margin-bottom: 2rem;

    &:last-of-type{
        border:none;
        margin-bottom: 0;
    }
}



.boton--blog{
    background-color: $negro;
}

@include phone{
    .blog, .sidebar{
        h3{
            text-align: center;
        }
    }

    .sidebar{
        margin-top: 5rem;
    }
}

//Sobre Nosotros

@include desktop{
    .sobre--nosotros{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 2rem;

        
        .sobre--nosotros__imagen{
            display: flex;
            align-items: center;
            
        }
        
    }
}

//Mis cursos

.curso{

    border-bottom: 1px solid gray ;
    margin-bottom: 1.5rem ;

    @include desktop{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2.5rem;
        .curso--imagen{
            display: block;
            width: 90%;
            margin: auto auto;
        }
    }

    &:last-of-type{
        border:none;
        margin-bottom: 0;
    }
    
}

@include phone{
    .curso--texto{
        h4{
            text-align: center;
        }
    }
}

//Entrada

.entrada{
    h3{
        text-align: center;
    }

    .entrada--imagen{
        text-align: center;
    }
}