.header{
    background-image: url(/build/img/Banner.webp);
    height: 50rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    .header__texto{
        color: $blanco;
        text-align: center;
        margin-top: 8rem;
    }

}

.barra{
    padding-top: 1.5rem;
}

@include desktop{

.barra{
    display: flex;
    justify-content: space-between;
    align-items: center;

    .navegacion{
        display: flex;
        gap: 2rem;
    }
    }
}

.logo__nombre{
        font-size: 4rem;
    }

.navegacion__enlace{
        display: block;
        text-align: center;
        color: $blanco;
        
}

@include phone{
    .navegacion{
        margin-top: 5rem;

        .navegacion__enlace{
            font-size: 2.3rem;
        }
    }

    
}



.logo{
    color: $blanco;
    text-align: center;
    
    .logo__nombre{
        font-weight: $semibold;
        
    }

    .logo__bold{
        font-weight: $bold;
        
    }
}