html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

p {
  font-size: 2rem;
  color: #000000;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  font-family: "Outfit", sans-serif;
  line-height: 1;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 3rem;
}

img {
  max-width: 100%;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.centrar-texto {
  text-align: center;
}

.boton {
  display: block;
  color: #ffff;
  text-align: center;
  padding: 1rem 3rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
  border: none;
}

.header {
  background-image: url(/build/img/Banner.webp);
  height: 50rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.header .header__texto {
  color: #ffff;
  text-align: center;
  margin-top: 8rem;
}

.barra {
  padding-top: 1.5rem;
}

@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .barra .navegacion {
    display: flex;
    gap: 2rem;
  }
}
.logo__nombre {
  font-size: 4rem;
}

.navegacion__enlace {
  display: block;
  text-align: center;
  color: #ffff;
}

@media (max-width: 768px) {
  .navegacion {
    margin-top: 5rem;
  }
  .navegacion .navegacion__enlace {
    font-size: 2.3rem;
  }
}
.logo {
  color: #ffff;
  text-align: center;
}
.logo .logo__nombre {
  font-weight: 300;
}
.logo .logo__bold {
  font-weight: 700;
}

@media (min-width: 768px) {
  .contenido_principal {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 6em;
  }

  .boton--blog {
    display: inline-block;
  }
}
.entrada {
  border-bottom: 1px solid gray;
  margin-bottom: 2rem;
}
.entrada:last-of-type {
  border: none;
  margin-bottom: 0;
}

.boton--blog {
  background-color: #000000;
}

@media (max-width: 768px) {
  .blog h3, .sidebar h3 {
    text-align: center;
  }

  .sidebar {
    margin-top: 5rem;
  }
}
@media (min-width: 768px) {
  .sobre--nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
  .sobre--nosotros .sobre--nosotros__imagen {
    display: flex;
    align-items: center;
  }
}
.curso {
  border-bottom: 1px solid gray;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .curso {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2.5rem;
  }
  .curso .curso--imagen {
    display: block;
    width: 90%;
    margin: auto auto;
  }
}
.curso:last-of-type {
  border: none;
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .curso--texto h4 {
    text-align: center;
  }
}
.entrada h3 {
  text-align: center;
}
.entrada .entrada--imagen {
  text-align: center;
}

.boton--sidebar {
  background-color: #f57a15;
  color: #ffff;
}

.cursos {
  list-style: none;
}

.widget-curso {
  border-bottom: 1px solid gray;
  margin-bottom: 2rem;
}
.widget-curso:last-of-type {
  border: none;
  margin: 0;
}
.widget-curso .widget-curso__label {
  font-weight: 700;
}
.widget-curso .widget-curso__info {
  font-family: "Outfit", sans-serif;
  font-weight: 300;
}

.footer {
  background-color: #000000;
  padding: 4rem;
}

.contacto-bg {
  background-image: url(/build/img/contacto.webp);
  height: 40rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.formulario {
  background-color: #ffff;
  margin: -5rem auto 0 auto;
  width: 88%;
  padding: 4rem;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  gap: 2rem;
}
.campo .campo__label {
  flex: 0 0 9rem;
  text-align: right;
}
.campo .campo__field {
  flex: 1;
  border: 1px solid #000000;
}
.campo .campo__field.campo__field--textarea {
  height: 20rem;
}

.boton:hover {
  cursor: pointer;
}