.boton--sidebar{
    background-color: $pricnipal;
    color: $blanco;
}

.cursos{
    list-style: none;
}

.widget-curso{
    border-bottom: 1px solid gray;
    margin-bottom: 2rem;

    &:last-of-type{
        border:none;
        margin: 0;
    }

    .widget-curso__label{
        font-weight: $bold;
    }

    .widget-curso__info{
        font-family: $fuentePrincipal;
        font-weight: $semibold;
    }
}